html {
  height: 100%;
}

.offcanvas-main,
.offcanvas-content,
.offcanvas-bar {
  box-sizing: border-box;
}

.offcanvas-main {
  position: relative;
  width: 100%;
  height: 100%;
}

.offcanvas-content {
  z-index: 1;
  position: relative;
  width: 100%;
  min-height: 100%;
  transition: transform 500ms ease;
}

.offcanvas-bar {
  z-index: 2;
  position: fixed;
  background: #000;
  color: #fff;
  transition: transform 500ms ease;
  overflow: auto;

  &[data-offcanvas-bar-position="left"],
  &[data-offcanvas-bar-position="right"] {
    top: 0;
    width: 50%;
    height: 100%;

    &.offcanvas-bar--open {
      transform: translateX(0);
    }
  }

  &[data-offcanvas-bar-position="left"] {
    left: 0;
    transform: translateX(-100%);
  }

  &[data-offcanvas-bar-position="right"] {
    right: 0;
    transform: translateX(100%);
  }

  &[data-offcanvas-bar-position="top"],
  &[data-offcanvas-bar-position="bottom"] {
    position: fixed;
    left: 0;
    width: 100%;
    height: 50%;

    &.offcanvas-bar--open {
      transform: translateY(0);
    }
  }

  &[data-offcanvas-bar-position="top"] {
    top: 0;
    transform: translateY(-100%);
  }

  &[data-offcanvas-bar-position="bottom"] {
    bottom: 0;
    transform: translateY(100%);
  }
}

.offcanvas-overlay {
  visibility: hidden;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #000;
  opacity: .75;
  transition: opacity 500ms ease;

  &--visible {
    visibility: visible;
  }
}